@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=IBM+Plex+Sans:wght@600;700&display=swap);
#passenger-public {
  background-image: url(/static/media/RiderHome.fd0c0b0f.jpeg);
  filter: blur(1px);
  -webkit-filter: blur(1px);
  background-position-y: -110px;
}

#driver-public {
  background-image: url(/static/media/DriverHome.91fa3ae2.jpeg);
  filter: blur(1px);
  -webkit-filter: blur(1px);
}

.passenger-bg {
  background-color: #30c8ea;
}

.driver-bg {
  background-color: #0dc2ad;
}

* {
  padding: 0;
  margin: 0;
}

html {
  height: 100%;
}

body {
  min-height: 100%;
  background-color: #000;
}

.navbar {
  background-color: #fff;
  position: absolute;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 14px 0px;
  z-index: 100;
}

.nav-logo {
  height: 58px;
  width: 152px;
  margin: 4px 30px 0px 30px;
  cursor: pointer;
}

.nav-links {
  display: flex;
  align-self: flex-end;
  grid-gap: 40px;
  gap: 40px;
  margin-bottom: 5px;
  margin-right: 80px;
  margin-left: auto;
}

.nav-link,
.nav-link:link,
.nav-link:visited {
  color: rgb(81, 49, 194);
  font-size: 34px;
  font-family: "Bebas Neue", cursive;
  text-decoration: none;
}

.sign-in-btn {
  padding: 12px 30px;
  border: 0;
  border-radius: 24px;
  font-size: 18px;
  font-family: "IBM Plex Sans", sans-serif;
  color: #fff;
  text-decoration: none;
  margin-right: 50px;
  cursor: pointer;
}

.image-overlay {
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 0;
}

.main {
  position: relative;
  font-family: "IBM Plex Sans", sans-serif;
  color: white;
  height: 590px;
  top: 95px;
  padding: 20px 50px;
}

.home-title {
  font-weight: bold;
  font-size: 70px;
  margin-top: 120px;
  margin-left: 100px;
}

.home-subtitle {
  font-weight: 600;
  font-size: 30px;
  margin-top: 12px;
  margin-left: 106px;
}

.public-register-btn {
  padding: 14px 42px;
  border: 0;
  border-radius: 28px;
  font-size: 24px;
  color: #fff;
  margin-right: 50px;
  cursor: pointer;
  margin-top: 32px;
  margin-left: 100px;
}

.passenger-btn {
  background-color: #30c8ea !important;
}

.driver-btn {
  background-color: #0dc2ad !important;
}

.login-box {
  background-color: #fff;
  color: #000;
  border-radius: 30px;
  width: 400px;
  margin: 80px auto;
  padding: 60px;
  padding-top: 40px;
}

.registration-box {
  background-color: #fff;
  color: #000;
  border-radius: 30px;
  width: 600px;
  margin: 40px auto;
  padding: 60px 40px;
}

.registration-title {
  text-align: center;
  margin-bottom: 30px;
}

.login-tab-switch {
  display: flex;
  font-size: 24px;
  padding: 0px 20px;
  margin-bottom: 20px;
  justify-content: space-evenly;
}

.login-tab {
  padding-bottom: 1px;
  text-decoration: none;
}

.login-tab.active {
  border-bottom: 2px solid black;
}

.login-form {
  display: flex;
  flex-direction: column;
  font-size: 24px;
}

.login-form .label {
  margin-top: 20px;
  margin-left: 12px;
}

.registration-form .label {
  margin-top: 10px;
  margin-left: 12px;
  width: 80px;
}

.login-form input {
  margin-top: 8px;
  background-color: #ededed;
  border: 0;
  border-radius: 24px;
  padding: 10px 20px;
  font-size: 18px;
}

.registration-form-row {
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
}

.registration-form input {
  margin-top: 4px;
  background-color: #ededed;
  border: 0;
  border-radius: 24px;
  padding: 10px 20px;
  font-size: 16px;
}

.login-form-actions {
  margin-top: 30px;
  padding: 0;
}

.registration-form-actions {
  margin-top: 30px;
  padding-left: 20%;
}

.login-form-actions input,
.registration-form-actions input {
  padding: 12px 36px;
  border: 0;
  border-radius: 28px;
  font-size: 18px;
  color: #fff;
  cursor: pointer;
}

.signup-text,
.signup-text:visited {
  margin-left: 68px;
  text-decoration: none;
  font-size: 16px;
}

.request-trip-section {
  display: flex;
  flex-direction: row;
  justify-content: center;
  grid-gap: 30px;
  gap: 30px;
  height: 100%;
  padding: 40px;
}

.request-trip-inner {
  background-color: #fff;
  border-radius: 25px;
  color: #000;
  height: 66%;
  width: 40%;
  padding: 60px;
}

.request-trip-inner h1 {
  text-align: center;
  margin-bottom: 28px;
}

.request-trip-inner .request-btn {
  margin: 30px 30% 0px 30%;
  font-size: 20px;
}

.passenger-map {
  height: 506px;
  width: 40%;
  border-radius: 25px;
}

.passenger-trip-box {
  background-color: #fff;
  border-radius: 25px;
  color: #000;
  height: 100%;
  width: 50%;
  margin: auto;
}

.passenger-loading-section {
  padding-top: 180px;
  font-size: 26px;
  text-align: center;
}

.passenger-loading-section p {
  margin-bottom: 50px;
}

.driver-dashboard {
  background-color: #fff;
  color: #000;
  border-radius: 30px;
  width: 600px;
  margin: 60px auto;
  padding: 40px 50px;
  min-height: 360px;
  text-align: center;
}

.driver-action-section {
  margin-top: 72px;
  font-family: "Bebas Neue", cursive;
  font-size: 26px;
}

.driver-action-section button {
  margin: 0;
  margin-top: 30px;
}

.driver-loading-section {
  padding-top: 70px;
}

.driver-loading-section p {
  margin-bottom: 20px;
}

.edit-form-actions {
  margin-top: 40px;
  padding-left: 120px;
}

.delete-acc {
  background-color: rgb(241, 75, 53);
}

.history-box {
  border-collapse: collapse;
  margin: auto;
  width: 70%;
  background-color: #fff;
  color: #000;
}

.history-box th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #4b72dd;
  color: white;
}

.history-box td,
.history-box th {
  border: 1px solid #ddd;
  padding: 10px;
  height: 36px;
}

.history-box tr:nth-child(even) {
  background-color: #f2f2f2;
}

.no-history-msg {
  background-color: #fff;
  color: #000;
  font-size: 26px;
  width: 70%;
  border-radius: 20px;
  margin: auto;
  padding: 20px;
  text-align: center;
}

/* Loading symbol */
.loader {
  z-index: 99;
  margin: 0 auto;
  border: 12px solid #f3f3f3; /* Light grey */
  border-top: 12px solid #30c8ea; /* Blue */
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: spin 1.5s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

